<template>
  <div class="info-container">
    <div class="info-wrapper">
      <div class="ads pc-hide" style="padding-top: 0;margin-top: 0px;">
        <div class="ads-item" v-for="item in _ads.slice(0, 8)">
          <van-image
            @click="goAd(item.link)"
            fit="cover"
            position="center"
            :lazy-load="true"
            :src="item.picUrl"
          >
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
        </div>
      </div>
      <div class="video-location h5-hide">
        <div class="line"></div>
        <span class="link" @click="goHome">HOME</span>
        <span class="arrow"></span>
        <span class="link" @click="goCategory">{{classifyName}}</span>
        <span class="arrow"></span>
        {{data.title}}
      </div>
      <div class="video-info">
        <div class="h5-video-location pc-hide">
          <div class="line"></div>
          <span class="link" @click="goCategory">{{classifyName}}</span>
          <span class="arrow"></span>
          {{data.title}}
        </div>
        <div id="mse"></div>
        <div class="video-wrapper">
          <div class="video-img" id="video-info"></div>
        </div>
        <div class="video-name">
           <span v-if="language == 'ko-KR'">
            {{data.title}}
          </span>
          <span v-if="language == 'en-US'">
            {{data.titleUs}}
          </span>
          <span v-if="language == 'zh-CN'">
            {{data.titleCn}}
          </span>
        </div>
        <div class="info">
          <div class="viewed">
            <img src="@/assets/imgs/view.png" alt="">
            <span> : {{data.watchTimes}}</span>
          </div>
          <div class="time">
            <img src="@/assets/imgs/flag.png" alt="">
            <span> : {{formatTime(data.createdAt)}}</span>
          </div>
        </div>
        <div class="video-desc" v-if="data.titleChinese">
          {{}}
          <!-- data.titleChinese -->
        </div>
      </div>
      <div class="ads" v-if="_ads.slice(0, 16).length">
        <div class="ads-item"  v-for="item in _ads.slice(0, 16)">
          <van-image
            @click="goAd(item.link)"
            fit="cover"
            position="center"
            :lazy-load="true"
            :src="item.picUrl"
          >
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
        </div>
      </div>
<!--      <Title class="page-title h5-hide" :name="relatedMap[language]" :more="false"></Title>-->
      <div class="list" v-if="videos.slice(0, 8).length">
        <Card :classifyName="classifyName" :classifyId="classifyId" :specialId="specialId" class="video-card" v-for="item in videos.slice(0, 8)" :item="item" :key="item.id"></Card>
      </div>
      <div class="ads" v-if="_ads.slice(16, 24).length">
        <div class="ads-item"  v-for="item in _ads.slice(16, 24)">
          <van-image
                  @click="goAd(item.link)"
                  fit="cover"
                  position="center"
                  :lazy-load="true"
                  :src="item.picUrl"
          >
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
        </div>
      </div>
      <div class="list" v-if="videos.slice(8, 16).length">
        <Card :classifyName="classifyName" :classifyId="classifyId" :specialId="specialId" class="video-card" v-for="item in videos.slice(8, 16)" :item="item" :key="item.id"></Card>
      </div>
      <div class="ads" v-if="_ads.slice(24,32).length">
        <div class="ads-item"  v-for="item in _ads.slice(24,32)">
          <van-image
                  @click="goAd(item.link)"
                  fit="cover"
                  position="center"
                  :lazy-load="true"
                  :src="item.picUrl"
          >
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
        </div>
      </div>
      <div class="list" v-if="videos.slice(16, 24).length">
        <Card :classifyName="classifyName" :classifyId="classifyId" :specialId="specialId" class="video-card" v-for="item in videos.slice(16, 24)" :item="item" :key="item.id"></Card>
      </div>
      <div class="ads" v-if="_ads.slice(32, 40).length">
        <div class="ads-item"  v-for="item in _ads.slice(32, 40)">
          <van-image
                  @click="goAd(item.link)"
                  fit="cover"
                  position="center"
                  :lazy-load="true"
                  :src="item.picUrl"
          >
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
        </div>
      </div>
      <!-- <div class="more" @click="goCategory">{{moreRelatedMap[language]}}</div> -->
      <Pagination
      :total-pages="total"
      :current-page="pageNum"
      @page-change="handlePageChange"
    />
    </div>
  </div>
</template>

<script>
  import * as api from '@/api'
  import moment from 'moment';
  import Card from "@/components/Card.vue";
  import Title from "@/components/Title.vue";
  import {mapActions,mapState} from 'vuex';
  import 'video.js/dist/video-js.css';
  import VideoJs from 'video.js';
  import Player from 'xgplayer';
  import 'xgplayer/dist/index.min.css';
  import HlsPlugin from 'xgplayer-hls'
  import MobilePreset from 'xgplayer/es/presets/mobile'
  import Pagination from "@/components/NewPage.vue";

  export default {
    name: "Info",
    components: {Title,Card,Pagination},
    data() {
      return {
        id: '',
        data: {},
        _ads: [],
        videos: [],
        player: null,
        relatedMap: {
          'ko-KR': '',
          'en-US': 'Related videos',
          'zh-CN': ''
        },
        moreRelatedMap: {
          'ko-KR': '   ',
          'en-US': 'More related videos',
          'zh-CN': ''
        },
        classifyName: '',
        classifyId: '',
        specialId: '',
        pageNum:1,
        pageSize:20,
        total:10
      }
    },
    computed: {
      ...mapState({
        ads: state => state.common.ads,
        menus: state => state.common.menus,
        language: state => state.common.language,
      }),
    },
    watch: {
      '$route': {
        handler(nv) {
          if(nv.name == 'info') {
            this.id = this.$route.query.id
            this.initData();

            this.classifyName = this.$route.query.classifyName
            this.classifyId = this.$route.query.classifyId
            this.specialId = this.$route.query.specialId
          }
        },
        immediate: true
      },
      ads(nv) {
        this._ads = JSON.parse(JSON.stringify(nv));
      }
    },
    methods: {
      ...mapActions({
        toggleLoading: 'common/toggleLoading',
      }),

            // 
      handlePageChange(newPage) {
        this.pageNum = newPage;
        this.getVideos()
      },


      goAd(url) {
        window.open(url)
      },
      formatTime(time) {
        return moment(time).format('YYYY-MM-DD')
      },
      async getVideoInfo() {
        this.toggleLoading(true);
        try {
          let {data, code} = await api.getVideoInfo({id: this.id})
          if (code == 200) {
            this.data = data;
            this.initPlayer()
            this.getMenuInfo()
          }
        } catch (e) {
        } finally {
          this.toggleLoading(false);
        }
      },
      async getMenuInfo() {
        try {
          let {data, code} = await api.getMenuInfo({id: this.data.classifyId})
          if (code == 200) {
            this.classifyInfo = data;
          }
        } catch (e) {
        } finally {
          this.toggleLoading(false);
        }
      },
      async getVideos() {
        // console.log('why this plus one?=>',this.data.specialId)
        try {
          let {rows, code,total} = await api.getVideos({
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            classifyId: this.data.classifyId,
            specialId: this.data.specialId
          })
          if (code == 200) {
            this.videos = rows
            this.total=total
          }
        } catch (e) {

        } finally {
        }
      },
      goHome() {
        this.$router.push('index')
      },
      goCategory() {
        this.$router.push({
          name: 'category',
          query: {
            classifyId: this.data.classifyId,
            specialId: this.data.specialId,
            classifyName: ''
          }
        })
      },
      initPlayer() {
        const config = {
          "id": "video-info",
          fluid: true,
          volume: 0.9,
          autoplay: false,
          videoInit: true,
          loop: true,
          // playbackRate: [0.5, 0.75, 1, 1.5, 2],
          defaultPlaybackRate: 1,
          "url": this.data.video,
          "playsinline": true,
          "poster": this.data.videoCover,
          "plugins": [HlsPlugin],
          "isLive": false,
          "playbackRate": [
                    null
          ],
          "rotate": {
                    "clockwise": true,
                    "innerRotate": true
          },
          "pip": true,
          rotateFullscreen: true
        }
        let player = new Player(config)
        // setTimeout(() => {
        // let player = new Player(config)},0)
        // document.getElementById('video-info').innerHTML = `<video id="video-js" class="video-js"></video>`
        // setTimeout(() => {
        //   this.player = VideoJs(document.getElementById('video-js'), {
        //     controls: true,
        //     muted: true,
        //     fluid: true,
        //     reload: "auto",
        //     poster: this.data.videoCover,
        //     sources: [
        //       {
        //         src: this.data.video,
        //         type: 'application/x-mpegURL',
        //       },
        //     ],
        //   }, function onPlayerReady() {
        //   })
        // }, 0)
      },
      initData() {
        if (this.player) {
          this.player.dispose()
        }
        this.id = this.$route.query.id
        this.getVideoInfo()
        if (this.menus && this.menus.length) {
          let count = (this.menus.length + 1) * 24 + 1;
          let times = Math.ceil(count / this.ads.length);
          let ads = [];
          for (let i = 0;i < times;i++) {
            ads = ads.concat(this.ads);
          }
          ads.shift()
          this._ads = ads;
        }
        this.$nextTick(() => {
          setTimeout(() => {
            this.getVideos(); //  1 
          }, 1000); // 1000  = 1 
        });
        
      }
    },
    beforeUnmount() {
      if (this.player) {
        this.player.dispose()
      }
    }
  }
</script>

<style scoped lang="scss">
  .info-container{
    width: 100%;
    display: flex;
    justify-content: space-between;
    .info-wrapper{
      width: 100%;
      padding-top: 23px;
      .video-location{
        background: #172231;
        height: 48px;
        @include clear();
        box-shadow: 0px 10px 10px rgba(255,255,225,0.1) inset,
        0px -10px 10px rgba(0,0,0,0.3) inset;
        margin-bottom: 60px;
        line-height: 48px;
        font-size: 24px;
        .line{
          width: 8px;
          height: 30px;
          background: $main-color;
          margin-top: 9px;
          float: left;
          margin-left: 23px;
          margin-right: 10px;
        }
        width: 100%;
        color: #fff;
        @include ellipsis;
        .link{
          transition: 0.3s ease;
          &:hover{
            color: $main-color;
          }
          cursor: pointer;
        }
        .arrow{
          margin: 0 20px;
        }
      }
      .video-info{
        width: 100%;
        margin-bottom: 110px;

        .h5-video-location{
          height: 50px;
          line-height: 50px;
          font-size: 18px;
          color: #fff;
          box-sizing: border-box;
          @include ellipsis;
          box-shadow: 0px 5px 5px rgba(255,255,225,0.1) inset,
          0px -5px 5px rgba(0,0,0,0.3) inset;
          background: #0e141d;
          margin-bottom: 30px;
          @include clear;
          .line{
            width: 5px;
            height: 36px;
            background: $main-color;
            margin-top: 7px;
            float: left;
            margin-left: 7px;
            margin-right: 10px;
          }
          .first{
            color: $main-color;
            margin-right: 10px;
          }
          @include ellipsis;
          .link{
            transition: 0.3s ease;
            &:hover{
              color: $main-color;
            }
            cursor: pointer;
          }
          .arrow{
            margin: 0 10px;
            position: relative;
            top: -1px;
          }
        }
        .video-wrapper{
          box-sizing: border-box;
          max-width: 75%;
          margin: 0 auto;
          background: #172231;
        }
        .video-img{
          width: 100%;
          background: #000;
          .video-js{
            width: 100%;
            /*object-fit: cover;*/
            /*object-position: center;*/
          }
        }
        .info-h5{
          width: 100%;
          font-size: 12px;
          color: #adadad;
          display: flex;
          justify-content: space-between;
          box-sizing: border-box;
          padding: 10px;
          .viewed{
            display: flex;
            align-items: center;
            img{
              margin-right: 5px;
            }
          }
        }
        .info{
          width: 700px;
          margin: 0 auto;
          height: 54px;
          box-sizing: border-box;
          line-height: 54px;
          display: flex;
          justify-content: space-between;
          font-size: 30px;
          color: #bababa;
          img{
            margin-right: 10px;
            vertical-align: middle;
          }
          span{
            vertical-align: middle;
          }
        }
        .video-name{
          width: 100%;
          box-sizing: border-box;
          font-size: 30px;
          color: #d0d0d0;
          height: 48px;
          line-height: 48px;
          margin-top: 30px;
          margin-bottom: 50px;
          @include ellipsis;
          box-sizing: border-box;
          text-align: center;
        }
        .video-desc{
          width: 100%;
          box-sizing: border-box;
          padding: 0 150px 0 25px;
          font-size: 24px;
          color: #8b8b8b;
        }
      }
      .list{
        width: 100%;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        flex-direction:row;
        box-sizing: border-box;
        padding-left: 20px;
        padding-right: 20px;
        .video-card{
          margin-bottom: 13px;
        }
      }
      .page-title{
        margin-bottom: 11px;
      }
      .ads{
        width: 100%;
        padding: 23px 20px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        box-sizing: border-box;
        padding-top: 0px;
        padding-bottom: 11px;
        .ads-item{
          width: calc((100% - 27px) / 4);
          margin-bottom: 7px;
          height: 125px;
          @include border2;
          box-sizing: border-box;
          cursor: pointer;
          border-radius: 2px;
          :deep(.van-image){
            width: 100%;
            height: 100%;
            .van-image__loading{
              background: none;
            }
            .van-image__error{
              background: none;
            }
          }
        }
      }
      .more{
        width: 788px;
        height: 74px;
        line-height: 74px;
        font-size: 30px;
        color: #fff;
        cursor: pointer;
        text-align: center;
        background: #343945;
        margin: 0 auto;
      }
    }
  }
</style>
<style lang="scss" scoped>
  @media screen and (min-width: 1280px) and (max-width: 1600px) {
    .info-container{
      .info-wrapper{
        .video-info{
          .video-img{}
          .video-desc{
            padding: 0 25px 0 25px;
          }
        }

      }
    }
  }
  @media screen and (min-width: 750px) and (max-width: 1280px) {
    .info-container{
      .info-wrapper{
        width: 750px;
        .video-info{
          .video-img{}
          .video-wrapper{
            padding-left: 50px;
            padding-right: 50px;
          }
          .info{
            height: 50px;
            line-height: 50px;
            font-size: 24px;
          }
          .video-name{
            font-size: 24px;
          }
          .video-desc{
            font-size: 21px;
            padding: 0 25px 0 25px;
          }
        }
        .ads{
          .ads-item{
            height: 100px;
          }
        }
        .more{
          width: 750px;
        }
      }
    }
    .sidebar{
      display: none;
    }
  }
  @media screen and (max-width: 750px) {
    .h5-hide{
      display: none!important;
    }
    .pc-hide{
      display: flex!important;
    }
    .info-container{
      width: 100%;
      .info-wrapper{
        width: 100%;
        box-sizing: border-box;
        padding-left: 2px;
        padding-right: 2px;
        .ads{
          padding-left: 14px;
          padding-right: 14px;
          .ads-item{
            width: 100%;
            height: 130px;
            margin-bottom: 6px;
            border: 1px solid #d7d7d8;
            &:nth-child(1), &:nth-child(2), &:nth-child(3), &:nth-child(4), {
              display: block;
            }
          }
        }
        .list{
          margin-bottom: 25px;
          .video-card{
            width: 100%;
            margin-bottom: 18px;
          }
        }
        .more{
          display: none;
        }
        .video-info{
          width: 100%;
          box-sizing: border-box;
          padding-bottom: 0px;
          margin-bottom: 70px;
          .pc-hide{
            display: block!important;
          }
          .video-wrapper{
            max-width: 100%;
            padding: 0px;
          }
          .info-h5{
            display: flex!important;
          }
          .info{
            width: 100%;
            font-size: 14px;
            padding-left: 40px;
            padding-right: 40px;
            height: 40px;
            line-height: 40px;
            img{
              height: 14px;
            }
          }
          .video-img{
            @include border;
          }
          .video-name{
            width: 100%;
            font-size: 16px;
            color: #fff;
            height: auto;
            padding: 0 13px;
            margin-top: 13px;
            line-height: 24px;
            margin-bottom: 20px;
          }
          .video-desc{
            font-size: 14px;
            color: #fff;
            padding: 10px;
            padding-top: 0px;
            img{
              height: 14px;
            }
          }
        }

      }
      .sidebar{
        display: none;
      }
    }
  }
</style>
<style lang="scss">
  @media screen and (max-width: 750px) {
    .xgplayer xg-icon svg{
      width: 16px;
    }
  }

</style>
