import util from "../../utils/util.js";
import "../../utils/debug.js";
import Plugin, { POSITIONS } from "../../plugin/plugin.js";
import BackSVG from "../assets/back.js";
class TopBackIcon extends Plugin {
  static get pluginName() {
    return "topbackicon";
  }
  static get defaultConfig() {
    return {
      position: POSITIONS.ROOT_TOP,
      index: 0
    };
  }
  afterCreate() {
    this.initIcons();
    this.onClick = (e) => {
      e.preventDefault();
      e.stopPropagation();
      this.config.onClick(e);
    };
    this.bind(["click", "touchend"], this.onClick);
  }
  registerIcons() {
    return {
      screenBack: { icon: BackSVG, class: "xg-fullscreen-back" }
    };
  }
  initIcons() {
    const { icons } = this;
    this.appendChild(this.root, icons.screenBack);
  }
  show() {
    util.addClass(this.root, "show");
  }
  hide() {
    util.removeClass(this.root, "show");
  }
  render() {
    return `<xg-icon class="xgplayer-back">
    </xg-icon>`;
  }
}
export { TopBackIcon as default };
