<template>
    <div class="pagination" >
      <!--  -->
      <button
        class="pagination-button"
        :disabled="currentPage <= 1"
        @click="goToPreviousTenPages"
      >
        <van-icon name="arrow-left" />
      </button>
  
      <!--  -->
      <div class="page-numbers" :key="componentKey">
        <span 
          v-for="page in visiblePages"
          :key="page"
          :class="{ active: activePages[page] }"
          @click="goToPage(page)"
        >
          {{ page }}
        </span>
      </div>
  
      <!--  -->
      <button
        class="pagination-button"
        :disabled="currentPage >= totalPages"
        @click="goToNextTenPages"
      >
        <van-icon name="arrow" />
      </button>
    </div>
  </template>
  
  <script>
  export default {
    name: "Pagination",
    props: {
      totalPages: {
        type: Number,
        required: true,
      },
      currentPage: {
        type: Number,
        required: true,
      },
    },
    data() {
      return {
        componentKey: 0,
      };
    },
    emits: ["page-change"],
    watch: {
      //  currentPage 
      // currentPage(newVal) {
      //   console.log('curennn,',newVal)
      //   this.updateActivePages(newVal);
      // },
      //  visiblePages 
      visiblePages: {
        handler(newVal) {
          this.updateActivePages(this.currentPage);
        },
        immediate: true,
      },
    },
    computed: {
      // 
      visiblePages() {
        const startPage = Math.floor((this.currentPage - 1) / 10) * 10 + 1;
        const endPage = Math.min(startPage + 9, this.totalPages);
        return Array.from({ length: endPage - startPage + 1 }, (_, i) => startPage + i);
      },
    },
    methods: {
    //  activePages 
    updateActivePages(currentPage) {

      //  activePages
      this.activePages = {};
      //  visiblePages active 
      this.visiblePages.forEach((page) => {
        // console.log('=>',page,currentPage)
        
        this.activePages[page] = page === currentPage;

        this.componentKey += 1;
      });
 

      },
      // 
      goToPage(page) {
        if (page >= 1 && page <= this.totalPages) {
          this.$emit("page-change", page);
        }
      },
      //  10 
      goToPreviousTenPages() {
        const newPage = Math.max(1, Math.floor((this.currentPage - 1) / 10) * 10 - 9);
        this.$emit("page-change", newPage);
      },
      //  10 
      goToNextTenPages() {
        const startPage = Math.floor((this.currentPage - 1) / 10) * 10 + 1;
        // console.log(startPage,'==>',this.currentPage);
        // alert(this.currentPage)
        const newPage = Math.min(this.totalPages, startPage + 10);
        this.$emit("page-change", newPage);
      }

    },
  };
  </script>
  
  <style scoped>
  .pagination {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    padding: 10px;
    width: 100%;
  }
  
  .pagination-button {
    padding: 10px 20px;
    border: 1px solid #ccc;
    background-color: #f9f9f9;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.3s;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .pagination-button:disabled {
    background-color: #e0e0e0;
    cursor: not-allowed;
  }
  
  .pagination-button:hover:not(:disabled) {
    background-color: #ddd;
  }
  
  .page-numbers {
    display: flex;
    gap: 5px;
  }
  
  .page-numbers span {
    padding: 10px 12px;
    border: 1px solid #ccc;
    cursor: pointer;
    color: white;
    border-radius: 4px;
    transition: background-color 0.3s;
  }
  
  .page-numbers span.active {
    background-color: #007bff;
    color: red;
    border-color: #007bff;
  }
  
  .page-numbers span:hover:not(.active) {
    background-color: #f1f1f1;
  }
  
  @media (max-width: 750px) {
    .pagination {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 21px;
        padding: 1px;
        width: 100%;
    }
    .pagination-button {
      padding: 3px 6px;
      border-radius: 3px;
    }
    .page-numbers {
      /* flex-wrap: wrap; */
      justify-content: center;
      gap: 2px;
      font-size: medium;
    }
    .page-numbers span {
      padding: 3px 5px; 
    }
  }
  </style>
  