    const { list, defaultDefinition } = this.config;
    if (list.length > 0) {
      let to = null;
      list.map((item) => {
        if (item.definition === defaultDefinition) {
          to = item;
        }
      });
      if (!to) {
        to = list[0];
      }
      this.changeDefinition(to);
    }
